import NumberUtils from './number-utils'

export default (function () {
  return  {
    name: [
      {required: true, message: ``, trigger: 'blur'},
      {max: 30, message: '30', trigger: 'blur'},
      {
        validator: (rule, value, callback) => {
          if (/^[\u4e00-\u9fa5_a-zA-Z0-9]+$/.test(value) === false) {
            callback(new Error('\'_\''))
          } else {
            callback()
          }
        }, trigger: 'blur'
      },
      {
        validator: (rule, value, callback) => {
          if (value && value.indexOf('_') === 0) {
            callback(new Error(''))
          } else {
            callback()
          }
        }, trigger: 'blur'
      }
    ],
    code: [
      {required: true, message: '', trigger: 'blur'},
      {max: 30, message: '30', trigger: 'blur'},
      {
        validator: (rule, value, callback) => {
          if (/^[a-zA-Z0-9_]+$/.test(value) === false) {
            callback(new Error('\'_\''))
          } else if (_.startsWith(value, '_')) {
            callback(new Error(''))
          } else {
            callback()
          }
        }, trigger: 'blur'
      }
    ],
    description: [
      {max: 300, message: '300 ', trigger: 'blur'}
    ],
    classify: [
      {max: 30, message: '30', trigger: 'blur'}
    ],
    isNotNULL: {
      required: true,
      trigger: 'blur',
      validator: (rule, value, callback) => {
        value = _.trim(value)
        if (!value) {//
          callback(new Error(''))
          return
        } else {
          callback()
        }
      }
    },
    isNumberInRange (valueScope) {
      return {
        validator: (rule, value, callback) => {
          if (!NumberUtils.isNumberInRange(value, valueScope)) {
            callback(new Error(''))
          } else {
            callback()
          }
        }
      }
    },
    isKeywordConflict () {
      return {
        validator: (rule, value, callback) => {
          const keyList = ['quota']
          if (keyList.indexOf(value)>-1) {
            callback(new Error(''))
          } else {
            callback()
          }
        },trigger: 'blur'
      }
    },
    isNumber: [
      {required: true, message: ``, trigger: 'blur'},
      {
        validator: (rule, value, callback) => {
          if (/^[0-9]*$/.test(value) === false) {
            callback(new Error(''))
          } else {
            callback()
          }
        }, trigger: 'blur'
      }
    ],
    isCellphoneNum: [
      {required: true, message: ``, trigger: 'blur'},
      {
        validator: (rule, value, callback) => {
          if (/^[1][0-9]{10}$/.test(value) === false) {
            callback(new Error(''))
          } else {
            callback()
          }
        }, trigger: 'blur'
      }
    ],
    isDateRange: [
      {
	    type: 'array',
	    required: true,
	    trigger: 'blur',
	    fields: {
	      0: {type: 'date', required: true, message: '', trigger: 'blur'},
	      1: {type: 'date', required: true, message: '', trigger: 'blur'}
	    }
	  }
    ]
  }
})()




