<template>
  <ApplyList :queryType="2">
    <el-button slot="trigger"
               type="primary"
               class="trigger"
               @click="transfer"></el-button>
    <el-dialog title=""
               size="tiny"
               :show-close="false"
               :visible="dialogShow">
      <el-form :model=transferForm
               ref="transferForm">
        <el-form-item label="">
          <el-radio-group v-model="transferForm.isSublicense">
            <el-radio :label="1"></el-radio>
            <el-radio :label="0"></el-radio>
          </el-radio-group>
        </el-form-item>
        <template v-if="transferForm.isSublicense">
          <el-form-item v-for="item in applyList"
                        :key="item.id"
                        :label="item.name"
                        :prop="'applyLevel'+item.id">
            <el-select v-model="transferForm['applyLevel'+item.id]"
                       filterable
                       clearable>
              <!-- <el-option label=""
                         value="" /> -->
              <el-option v-for="user in item.userInfos"
                         :key="user.userId"
                         :label="user.account+'('+user.nickName+')'"
                         :value="user.userId" />
            </el-select>
          </el-form-item>
        </template>
      </el-form>
      <div slot="footer"
           class="dialog-footer">
        <el-button type="primary"
                   @click="saveForm"> </el-button>
        <el-button @click="dialogShow=false"> </el-button>
      </div>
    </el-dialog>
  </ApplyList>
</template>
<script>
// 
import ApplyList from '@/components/apply/ApplyList.vue'
import comApi from '@/scripts/api.js'
export default {
  data() {
    return {
      dialogShow: false,
      applyList: [],
      transferForm: {
        isSublicense: 0
      }
    }
  },
  methods: {
    saveForm() {
      let nodes = []
      let isSublicense = this.transferForm.isSublicense
      for (let key in this.transferForm) {
        //nodes,,,
        if (key != 'isSublicense' && this.transferForm[key]) {
          let auditId = key.slice(10) * 1
          nodes.push({
            auditId,
            authId: this.transferForm[key],
            isSublicense,
          })
        }
      }
      //:
      if (isSublicense) {
        if (nodes.length) {
          this.doneSave(nodes)
        } else {
          this.$message.warning("")
        }
      }
      //:
      else {
        //.nodesisSublicense: 0,
        // isSublicense: 0,
        if (!nodes.length) {
          nodes.push({
            isSublicense: 0
          })
        }
        this.doneSave(nodes)
      }
    },
    transfer() {
      comApi.apply.getSublicense().then((res) => {
        if (res.nodes.length) {
          this.dialogShow = true
          this.applyList = res.nodes
          //
          this.transferForm.isSublicense = res.isSublicense
          res.nodes.forEach(item => {
            this.$set(this.transferForm, 'applyLevel' + item.id, item.authId)
            // this.transferForm['applyLevel' + item.id] = item.authId
          })
        } else {
          this.$message.warning(",")
        }
      }).catch(() => {
        this.$message.warning("")
      })
    },
    doneSave(nodes) {
      comApi.apply.saveSublicense(nodes).then(() => {
        this.dialogShow = false
      })
    }
  },
  components: {
    ApplyList
  }
}
</script>
<style lang="scss" scoped>
.el-button.trigger {
  margin-bottom: 20px;
}
</style>




