<template>
  <el-row>
    <el-col :span="12">
      <el-form-item label="ID">{{baseInfo.id}}</el-form-item>
    </el-col>
    <el-col :span="12">
      <el-form-item label="">{{formatDate(baseInfo.created)}}</el-form-item>
    </el-col>
    <el-col :span="12">
      <el-form-item label="">{{typeOption[baseInfo.type]}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.targetEngineCode">
      <el-form-item label="">{{baseInfo.detailShow.targetEngineCode}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.targetEngineName">
      <el-form-item label="">{{baseInfo.detailShow.targetEngineName}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.sourceVersionName">
      <el-form-item label="">{{baseInfo.detailShow.sourceVersionName}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.targetVersionName && baseInfo.type==5">
      <el-form-item label="">{{baseInfo.detailShow.targetVersionName}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.targetVersionName && baseInfo.type!=5">
      <el-form-item label="">{{baseInfo.detailShow.targetVersionName}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.targetVersionDesc!=null && baseInfo.type!=5">
      <el-form-item label="">{{baseInfo.detailShow.targetVersionDesc==''?'':baseInfo.detailShow.targetVersionDesc}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.datacenterName">
      <el-form-item label="">{{baseInfo.detailShow.datacenterName}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.sourceEngineCode">
      <el-form-item label="">{{baseInfo.detailShow.sourceEngineCode}}</el-form-item>
    </el-col>
    <el-col :span="12"
            v-if="baseInfo.detailShow.sourceEngineName">
      <el-form-item label="">{{baseInfo.detailShow.sourceEngineName}}</el-form-item>
    </el-col>
    <el-col :span="12">
      <el-form-item label="" class="break-all">{{baseInfo.reason==''?'':baseInfo.reason}}</el-form-item>
    </el-col>
    <el-col :span="12" v-if="baseInfo.detailShow.link!=null">
      <el-form-item label="" class="break-all">
        <a href="#" v-click="showMessage" v-if="baseInfo.detailShow.link==''"></a>
        <a :href="baseInfo.detailShow.link" target="_blank" v-else></a>
      </el-form-item>
    </el-col>
  </el-row>
</template>
<script>
import applyOptions from './apply-option.mock.js'
import DateUtil from '@/scripts/date-utils'
export default {
  props: {
    baseInfo: {
      type: Object,
      default: () => ({
        detailShow: {}
      })
    }
  },
  data() {
    return {
      typeOption: applyOptions.type
    }
  },
  methods: {
    formatDate(date) {
      return DateUtil.formate(date)
    },
    showMessage(){
      this.$message.warning('')
    }
  }
}
</script>




